import React, { useState, useEffect } from "react";
import "./OpeningParagraph.css";
import TextAndInput from "./TextAndInput";
import { Paragraph, TextRun, AlignmentType } from "docx";

function OpeningParagraph({ updateDoc }) {
  const [meterial, setMeterial] = useState("");
  const [locationType, setLocationType] = useState("");
  const [customLocationType, setCustomLocationType] = useState("");
  const [location, setLocation] = useState("");

  const [recipient, setRecipient] = useState("");
  const [date, setDate] = useState("");
  const [contactPerson, setContactPerson] = useState("");
  const [project, setProject] = useState("");

  const [surveyDate, setSurveyDate] = useState("");
  const [engineer, setEngineer] = useState("");
  const [opinion, setOpinion] = useState("");

  useEffect(() => {
    const savedMeterial = localStorage.getItem("6");
    const savedLocationType = localStorage.getItem("7");
    
    if (savedMeterial) setMeterial(savedMeterial);
    if (savedLocationType) setLocationType(savedLocationType);
  }, []);

  // Update doc elements whenever any of the form fields change
  useEffect(() => {
    const docElements = [
      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `: ${localStorage.getItem(":") || recipient || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `\u202B:${localStorage.getItem(":") || date || "_________"}\u202C`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `: ${localStorage.getItem(":") || contactPerson || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `: ${localStorage.getItem(":") || project || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
      new Paragraph({ text: "", spacing: { after: 200 } }),

      new Paragraph({
        alignment: AlignmentType.CENTER,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `: "    ${
              localStorage.getItem("6") || meterial || "_________"
            } `,
            font: "David",
            size: 32,
            bold: true,
            underline: true,
            rtl: true,
            language: "he-IL",
          }),
          new TextRun({
            text: `${
              locationType === "" && 
              (localStorage.getItem("1") || customLocationType)
                ? (localStorage.getItem("1") || customLocationType)
                : (localStorage.getItem("7") || locationType || "_________")
            } ${localStorage.getItem("2") || location || "_________"}`,
            font: "David",
            size: 32,
            bold: true,
            underline: true,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({ text: "", spacing: { after: 200 } }),

      new Paragraph({
        alignment: AlignmentType.RIGHT,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: ` ${localStorage.getItem("3") || surveyDate || "_________"}    - ${
              locationType === "" && 
              (localStorage.getItem("1") || customLocationType)
                ? (localStorage.getItem("1") || customLocationType)
                : (localStorage.getItem("7") || locationType || "_________")
            } " "  ${localStorage.getItem("4") || engineer || "_________"}`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.RIGHT,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `      ${
              localStorage.getItem("6") || meterial || "_________"
            }  - ${
              locationType === "" && 
              (localStorage.getItem("1") || customLocationType)
                ? (localStorage.getItem("1") || customLocationType)
                : (localStorage.getItem("7") || locationType || "_________")
            } ${localStorage.getItem("2") || location || "_________"}      ${
              localStorage.getItem("5") || opinion || "_________"
            }`,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),

      new Paragraph({
        alignment: AlignmentType.RIGHT,
        spacing: { line: 360, after: 200 },
        children: [
          new TextRun({
            text: `           -  / /   `,
            font: "David",
            size: 24,
            rtl: true,
            language: "he-IL",
          }),
        ],
      }),
    ];


    // Pass the generated doc elements to the parent
    updateDoc(docElements);
  }, [
    recipient,
    date,
    contactPerson,
    project,
    meterial,
    locationType,
    customLocationType,
    location,
    surveyDate,
    engineer,
    opinion,
    updateDoc,
  ]);

  return (
    <>
      <div className="initial-info-container">
        <div className="half-cont">
          <TextAndInput
            label=":"
            type="date"
            value={date}
            setValue={setDate}
          />
          <TextAndInput
            label=":"
            value={project}
            setValue={setProject}
          />
        </div>
        <div className="half-cont">
          <TextAndInput
            label=":"
            value={recipient}
            setValue={setRecipient}
          />
          <TextAndInput
            label=":"
            value={contactPerson}
            setValue={setContactPerson}
          />
        </div>
      </div>
      <div className="text-paragraph-top">
        <div className="form-group">
          <label htmlFor="meterials" className="form-label">
            : "    
          </label>
          <select
            name="meterials"
            id="meterials"
            value={meterial}
            onChange={(e) => {
              setMeterial(e.target.value);
              localStorage.setItem("6", e.target.value);
            }}
            className="form-select"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=" "> </option>
          </select>
        </div>

        <div className="form-group">
          <label htmlFor="locations" className="form-label">
            :
          </label>
          <select
            name="locations"
            id="locations"
            value={locationType}
            onChange={(e) => {
              setLocationType(e.target.value);
              localStorage.setItem("7", e.target.value);
              if (e.target.value !== "") {
                setCustomLocationType("");
                localStorage.removeItem("1"); // Clear custom location type if not ""
              }
            }}
            className="form-select"
          >
            <option value=""> </option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
            <option value=""></option>
          </select>

          {locationType === "" && (
            <TextAndInput
              label={1}
              value={customLocationType}
              setValue={setCustomLocationType}
              type="text"
              placeholder="  "
            />
          )}

          <TextAndInput
            label={2}
            value={location}
            setValue={setLocation}
            type="text"
            placeholder=" "
          />
        </div>
      </div>
      <div className="text-paragraph-bottom">
        <p>
          {" "}
          <TextAndInput
            label={3}
            type="date"
            value={surveyDate}
            setValue={setSurveyDate}
          />{" "}
             -{" "}
          {(locationType === "" && 
            (localStorage.getItem("1") || customLocationType))
            ? (localStorage.getItem("1") || customLocationType)
            : (localStorage.getItem("7") || locationType || "_________")}{" "}
          " " {" "}
          <TextAndInput 
            label={4}
            value={engineer} 
            setValue={setEngineer} 
          />.
        </p>
        <p>
               ' {localStorage.getItem("6") || meterial || "_________"}  -{" "}
          {(locationType === "" && 
            (localStorage.getItem("1") || customLocationType))
            ? (localStorage.getItem("1") || customLocationType)
            : (localStorage.getItem("7") || locationType || "_________")}{" "}
          {localStorage.getItem("2") || location || "_________"}     {" "}
          <TextAndInput 
            label={5}
            value={opinion} 
            setValue={setOpinion} 
          />.
        </p>
        <p>
                    
          ( ,  /   ').
        </p>
      </div>
    </>
  );
}

export default OpeningParagraph;
